:root {
  color: #2f4f4f;
  font-family: Arial, sans-serif;
  font-size: 20px;
}

body {
  position: relative;
}

.people {
  --column: 2;
  grid-template-columns: repeat(var(--column), 200px);
  column-gap: 30px;
  width: fit-content;
  margin: 0 auto;
  padding-block: 30px 20px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --column: 4;
  }
}

.person {
  width: 200px;
  transition: transform 1s;
  position: relative;
}

.person:hover {
  z-index: 2;
  background-color: #eee;
  border-radius: 10px;
  margin: -10px -10px -20px;
  padding: 10px 10px 20px;
  transform: scale(1.2);
}

.person:hover .person__extra {
  opacity: 1;
  transition: all;
  transform: scaleY(1);
}

.person__photo-container {
  display: flex;
  position: relative;
}

.person__photo {
  aspect-ratio: 1;
  border-radius: 50%;
  width: 100%;
}

.person__rank {
  box-sizing: border-box;
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-self: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 1.15;
  display: flex;
  position: absolute;
  right: 10px;
}

.person__name {
  text-align: center;
  color: inherit;
  font-size: inherit;
  display: block;
}

.person__extra {
  text-align: center;
  color: #000;
  opacity: 0;
  transform-origin: top;
  margin-top: 8px;
  transition: transform .5s, opacity 1s;
  transform: scaleY(0);
}

.person__sport:after {
  content: ",";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}
